 import axios from 'axios'
export default{
    data(){
        return {
            keyword:'',
            lists:[]
        }
    },
    methods:{
        getData(){
            axios({
                method:'get',
                url:'https://api.douban.com/v2/movie/search'+`?q=${this.keyword}`
            }).then((res)=>{
                this.lists = res.data.subjects
                console.log(this.lists)
            }).catch(error => {
                console.log(error)
            })
        }
    }
}


